/* Basic editor styles */
.ProseMirror {
  outline: none;
  min-block-size: 100px;
  overflow-y: auto;
  padding: 1.25rem;
  inline-size: 100%;
}
.ProseMirror > * + * {
    margin-block-start: 0.75em;
  }
.ProseMirror p.is-editor-empty:first-child::before {
    block-size: 0;
    color: var(--mui-palette-text-disabled);
    content: attr(data-placeholder);
    float: inline-start;
    pointer-events: none;
  }
.ProseMirror ul,
  .ProseMirror ol {
    padding-block: 0;
    padding-inline: 1rem;
  }
.ProseMirror h1,
  .ProseMirror h2,
  .ProseMirror h3,
  .ProseMirror h4,
  .ProseMirror h5,
  .ProseMirror h6 {
    line-height: 1.1;
  }
.ProseMirror code {
    background-color: rgba(#616161, 0.1);
    color: #616161;
  }
.ProseMirror pre {
    background: #0d0d0d;
    color: #fff;
    font-family: 'JetBrainsMono', monospace;
    padding-block: 0.75rem;
    padding-inline: 1rem;
    border-radius: 0.5rem;
  }
.ProseMirror pre code {
      color: inherit;
      padding: 0;
      background: none;
      font-size: 0.8rem;
    }
.ProseMirror img {
    max-inline-size: 100%;
    block-size: auto;
  }
.ProseMirror blockquote {
    padding-inline-start: 1rem;
    border-inline-start: 2px solid var(--mui-palette-divider);
  }
.ProseMirror hr {
    border: none;
    border-block-start: 2px solid var(--mui-palette-divider);
    margin-block: 2rem;
    margin-inline: 0;
  }
